<template>
  <div class="section" @click="closeModal">
    <div class="wrapper" @click.stop>
      <div class="card">
        <div class="upper">
          <div class="text">
            <span class="category">{{ item.category }}</span>
            <span class="date">
              {{ item.date }}
            </span>
          </div>
          <span class="seperator"></span>
        </div>

        <div class="lower">
          <div
            :class="[
              'img_box',
              item.isImageSmallFrame ? 'small_frame' : 'big_frame',
            ]"
          >
            <v-icon
              :class="['arrow', 'leftArrow']"
              @click="prevImage"
              v-if="currentIdx != 0"
              >mdi-chevron-left</v-icon
            >
            <div
              :class="[
                'img',
                item.isImageSmallFrame ? 'vertical_size' : 'horizontal_size',
              ]"
            >
              <img
                v-for="img in item.image"
                :key="img"
                :src="img"
                alt="news"
                :class="['image', 'object-cover']"
              />
            </div>
            <v-icon
              :class="['arrow', 'rightArrow']"
              @click="nextImage"
              v-if="currentIdx != item.image.length - 1"
              >mdi-chevron-right</v-icon
            >
          </div>
          <div
            :class="[
              'content_box',
              item.isImageSmallFrame ? 'small_content' : 'big_content',
            ]"
          >
            <div class="title">
              <div class="title_text">{{ item.title }}</div>
            </div>
            <div class="content">
              <div v-html="item.content" class="content_text"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    selectedItem: {
      type: Object,
    },
  },
  data() {
    return {
      currentIdx: 0,
    };
  },
  computed: {
    item() {
      return this.selectedItem;
    },
  },
  methods: {
    closeModal() {
      this.$emit("isclicked");
    },
    nextImage() {
      if (this.currentIdx === this.item.image.length - 1) return;
      this.currentIdx++;

      const imgWidth = document.querySelector(".img").clientWidth;
      document.querySelector(".img").style.transform = `translateX(-${
        this.currentIdx * imgWidth
      }px)`;
    },
    prevImage() {
      if (this.currentIdx === 0) return;
      this.currentIdx--;

      const imgWidth = document.querySelector(".img").clientWidth;
      document.querySelector(".img").style.transform = `translateX(-${
        this.currentIdx * imgWidth
      }px)`;
    },
  },
};
</script>

<style src="./MobileCard.css" scoped></style>
