<template>
  <div
    v-for="(item, index) in filteredNews"
    :key="index"
    class="list"
    @mouseover="mouseOverItem(index)"
    @mouseleave="mouseLeaveItem(index)"
    @click="selectItem(item)"
  >
    <img :src="item.image[0]" alt="news" class="image" />
    <div
      :class="[isHover & (hoverIdx == index) ? '--active' : '', 'hover_box']"
    >
      {{ item.title }}
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  emits: ["selectedItem"],
  data: () => ({
    isHover: false,
    hoverIdx: null,
  }),
  computed: {
    ...mapState(["menuNum"]),
    ...mapGetters(["filteredNews"]),
  },
  methods: {
    selectItem(item) {
      this.$emit("selectedItem", item);
    },
    mouseOverItem(index) {
      this.isHover = true;
      this.hoverIdx = index;
    },
    mouseLeaveItem() {
      this.isHover = false;
      this.hoverIdx = null;
    },
  },
};
</script>

<style src="./ItemList.css" scoped></style>
