<template>
  <div class="container">
    <div class="wrapper">
      <ItemList @selectedItem="getSelectedItem" />
      <DeskTopCard
        v-if="isclicked && !isMobileSize"
        :selectedItem="this.selectedItem"
        @isclicked="closeModal"
      />
      <MobileCard
        v-if="isclicked && isMobileSize"
        :selectedItem="this.selectedItem"
        @isclicked="closeModal"
      />
    </div>
  </div>
</template>

<script>
import ItemList from "./newsList/ItemList.vue";
import DeskTopCard from "./card/DeskTopCard.vue";
import MobileCard from "./card/MobileCard.vue";
export default {
  data: () => ({
    selectedItem: [],
    isclicked: false,
    isMobileSize: false,
  }),
  components: {
    ItemList,
    DeskTopCard,
    MobileCard,
  },
  mounted() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    handleResize() {
      if (window.innerWidth < 760) {
        this.isMobileSize = true;
      } else {
        this.isMobileSize = false;
      }
      console.log(this.isMobileSize);
    },
    getSelectedItem(item) {
      document.body.style.position = "fixed";
      this.selectedItem = item;
      this.isclicked = true;
    },
    closeModal() {
      document.body.style.position = "";
      this.isclicked = false;
      console.log("close");
    },
  },
};
</script>

<style src="./Content.css" scoped>
.container {
  padding-top: 60px;
  padding-bottom: 42px;
  background-color: #f8f8f8;
}
</style>
